<script>
import axios from "axios";
import md5 from "md5";
import firebase from "firebase";
let db = firebase.firestore();
// let rdb = firebase.database()
import autophp from "@/plugins/autophp";
let sdb = new autophp();
export default {
  data() {
    return {
      skpds: [],
      users: [],
      users2: [],
    };
  },
  methods: {
    skpd() {
      let skpd_lama;
      let skpd_baru;
      let that = this;
      sdb
        .collection("skpd")
        .doc()
        .select(`select nama_skpd from skpd`)
        .then((res) => {
          skpd_lama = res;
          sdb
            .collection("tb_skpd")
            .doc()
            .select(`select skpd_id,skpd_nama from tb_skpd`)
            .then((res) => {
              skpd_baru = res;
              skpd_baru = skpd_baru.map((e) => {
                return {
                  id:e.skpd_id,
                  nama_skpd: e.skpd_nama,
                };
              });
              let news = this.$_.differenceBy(
                skpd_baru,
                skpd_lama,
                "nama_skpd"
              );
              console.log(skpd_lama);
              console.log(skpd_baru);
              that.skpds = news;
              console.log("baru", news);
            });
        });
    },
    proses_skpd() {
      console.log(this.skpds);
      this.skpds.forEach((e) => {
        sdb
          .collection("skpd")
          .doc()
          .set({ id_skpd:e.id,nama_skpd: e.nama_skpd })
          .then((res) => {
            console.log(res);
          });
      });
    },
    importUser() {
      let user_lama;
      let user_baru;
      let that = this;
      sdb
        .collection("tbuser")
        .doc()
        .select(`select nip,nama from tbuser`)
        .then((res) => {
          user_lama = res;
          sdb
            .collection("tb_pegawai")
            .doc()
            .select(
              `select pegawai_nip as nip,pegawai_nama as nama from tb_pegawai`
            )
            .then((res) => {
              user_baru = res;
              let news = this.$_.differenceBy(user_baru, user_lama, "nip");
              console.log(user_lama);
              console.log(user_baru);
              that.users = news;
              console.log("baru", news);
            });
        });
    },
    async proses_user() {
      
      console.log(this.users);
      for(let i = 0;i<this.users.length;i++){
        console.log(i,this.users[i])
      }
      // this.users.forEach((e) => {
      //   sdb
      //     .collection("tbuser")
      //     .doc()
      //     .set({ nip: e.nip, nama: e.nama })
      //     .then((res) => {
      //       console.log(res);
      //     });
      // });
    },
    importUser2() {
      let user_lama;
      let user_baru;
      let that = this;
      sdb
        .collection("tbuser")
        .doc()
        .select(`select nip,nama,skpd from tbuser`)
        .then((res) => {
          user_lama = res;
          sdb
            .collection("tb_pegawaikontrak")
            .doc()
            .select(
              `select pegawaikontrak_nik as nip,pegawaikontrak_nama as nama from tb_pegawaikontrak`
            )
            .then((res) => {
              user_baru = res;
              let news = this.$_.differenceBy(user_baru, user_lama, "nip");
              console.log(user_lama);
              console.log(user_baru);
              that.users2 = news;
              console.log("baru", news);
            });
        });
    },
    proses_user2() {
      console.log(this.users);
      this.users.forEach((e) => {
        sdb
          .collection("tbuser")
          .doc()
          .set({ 
            nip: e.nip,
            nama: e.nama,
            nohp:'-',
            unit_kerja:'-',
            unit_kerja:e.nip,
            password:md5(e.nip),
            jabatan:'-',
            email:'-',
            pertama:false,
            changepassword:true,
            fcm:'-',
            gambar:'http://balangankab.go.id/icon.png',
            displayName:e.nama,
            atasan:null,
            unit:null,
            emails:null,


            })
          .then((res) => {
            console.log(res);
          });
      });
    },
  },
  mounted() {},
  layout: "admin",
};
</script>